body {
  margin: 0;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@keyframes rgb {
  0%{
    box-shadow: 1px 1px 10px red;
    border: 5px solid red;
  }50%{
    box-shadow: 1px 1px 20px green;
    border: 5px solid green;
  }100%{
    box-shadow: 1px 1px 30px blue;
    border: 5px solid blue;
  }
}

@keyframes subir-bajar{
  0%{

  }
  50%{
    transform: translateY(-80px);
  }
  100%{
    transform: translateY(0px);
  }
}

@keyframes derecha-izquierda{
  0%{

  }
  50%{
    transform: translateX(180px);
  }
  100%{
    transform: translateX(0px);
  }
}

.subrayado{
  text-decoration:line-through;
  cursor: pointer;
}

.no-subrayado{
  cursor: pointer;
}

/*Inicio de la app*/
.principal-container{
  display: flex;
  flex-direction: column;
  align-items: center;

  background-color: #000000;
  background-image: url("data:image/svg+xml,%3Csvg width='6' height='6' viewBox='0 0 6 6' xmlns='http://www.w3.org/2000/svg'%3E%3Cg fill='%23e07215' fill-opacity='0.4' fill-rule='evenodd'%3E%3Cpath d='M5 0h1L0 6V5zM6 5v1H5z'/%3E%3C/g%3E%3C/svg%3E");}

/*INICIO DEL NAV*/
.nav-container{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;

  width: 100%;
  background-color: black;
}

.nav-container img{
  margin-top: 5px;
  margin-bottom: 5px;
  width: 300px;
}

.nav-container h1{
  color: #FD9A2C;
  font-weight: bolder;
  text-shadow: 1px 1px 5px #FD9A2C;
  text-align: center;
  margin-left: 5px;
  margin-right: 5px;
}

.nav-container ul{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;

  list-style: none;
}

.nav-container ul li{
  margin-left: 10px;
  margin-right: 10px;
}

.nav-container ul li a{
  color: #FD9A2C;
  text-decoration: none;
  font-size: 25px;
  font-weight: bolder;
}

/* INICIO DEL CONTAINER TAREAS*/

.tareas-container{
  margin-left: 5px;
  margin-right: 5px;
  background-color: white;
  margin-top: 30px;
  margin-bottom: 30px;
  border: 5px solid #FD9A2C;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 30px;

}

form{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.boton-agregar{
  margin-top: 20px !important;
  margin-bottom: 20px;
  width: 80%;
}

.cantidad-tareas{
  background-color: #FD9A2C;
  text-align: center;
  border: 2px solid black;
  border-radius: 20px;
  font-weight: bolder;
  box-shadow: 0px 0px 5px black;
  padding-top: 5px;
  padding-bottom: 5px;
  text-shadow: 1px 1px 2px black;
  margin-bottom: 40px;
}

.agregar-tarea{
  text-align: center;
}

.todos-container{
  border: 3px solid black;
  padding: 20px;
  border-radius: 20px;
}

/*INICIO DEL BOTON DESCARGAR*/
.boton-descargar{
  margin-left: 5px;
  margin-right: 5px;
  padding: 20px;
  border-radius: 20px;
  overflow: hidden;
  border: 3px solid #FD9A2C;
  margin-bottom: 30px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.boton-descargar:hover{
  background-color: rgb(112, 105, 105);
}

.boton-descargar i{
  font-size: 30px;
}

.boton-descargar h4{
  font-weight: bolder;
  margin-left: 15px;
  font-size: 30px;
}

.boton-descargar:hover i{
  animation: subir-bajar 800ms linear calc(1);
  color: white;
}

.boton-descargar:hover h4{
  animation: derecha-izquierda 800ms linear calc(1);
  color: white;
}

/*INICIO REDES*/
.redes-container{
  margin-left: 5px;
  margin-right: 5px;
  background-color: white;
  margin-bottom: 30px;
  border: 5px solid #FD9A2C;
  padding: 40px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  border-radius: 30px;
}

.h3redes{
  text-align: center;
  font-size: 35px;
  font-weight: bolder;
  text-decoration: underline;
}

.redes-container a{
  font-size: 45px;
  text-align: center;
  color: black;
}

.redes-container a:hover{
  transition: all 500ms;
  transform: scale(1.2 , 1.2);
  color: #FD9A2C;
}

footer{
  width: 100%;
  background-color: black;
}

footer h1{
  margin-top: 20px;
  margin-bottom: 20px;
  text-align: center;
  color: white;
}

@media screen and (max-width: 1200px) {
  
  nav{
    flex-direction: column !important;
  }

  nav img{
    margin-top: 30px !important;
  }

  nav h1{
    margin-top: 30px;
    margin-bottom: 30px;
  }

  nav ul{
    margin-bottom: 30px;
  }

}

@media screen and (max-width: 450px){
  nav ul{
    flex-direction: column !important;
  }
  nav ul li{
    margin-top: 5px;
    margin-bottom: 5px;
  }
  nav ul li a{
    font-size: 30px !important;
  }
}

@media screen and (max-width: 330px){
  .nav-container img{
    width: 250px;
  }

  .cantidad-tareas{
    font-size: 20px;
  }

  .todos-container li{
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

}